.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* Style for the product card container */
.container {
  margin-top: 20px;
}

/* Style for the product card */
.card {
  border: 1px solid #ddd;
  border-radius: 5px;
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s;
  width: 100%;
  max-width: 300px; /* Adjust the max width as needed */
  margin: 0 auto;
  background-color: #fff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

/* Style for the image container */
.image-container {
  position: relative;
  overflow: hidden;
}

/* Style for the discount label */
.discount {
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: #e74c3c;
  color: #fff;
  padding: 5px 10px;
  border-radius: 3px;
  font-size: 14px;
}

/* Style for the heart icon (wishlist) */
.wishlist {
  position: absolute;
  top: 5px;
  left: 5px;
  font-size: 20px;
  color: #e74c3c;
  cursor: pointer;
}

/* Style for the product image */
.card-img-top {
  max-height: 200px;
  object-fit: cover; /* Preserve aspect ratio and cover container */
  width: 100%;
}

/* Style for the product title */
.card-title {
  font-size: 18px;
  margin: 10px;
  color: #333;
}

/* Style for the product price */
.price {
  font-size: 16px;
  margin: 0 10px;
  color: #e74c3c;
}

/* Style for the product rating */
.rating {
  font-size: 14px;
  margin: 0 10px;
  color: #666;
}

/* Style for the "Add to Cart" button */
.btn-primary {
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
  cursor: pointer;
  margin: 10px;
  display: block;
  width: 80%;
  margin: 0 auto;
}

/* Hover effect for the product card */
.card:hover {
  transform: scale(1.05); /* Increase size on hover */
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.1); /* Add a subtle shadow */
}

.btn-delete {
  background-color: red;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
  cursor: pointer;
  margin-top: 10px;
}